@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap);
@font-face {
  font-family: digital-7;
  src: url("https://cdn.rawgit.com/lcneves/pomodoro-clock/master/digital-7%20(mono%20italic).ttf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eaebf3;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Calculator{margin:0 auto;margin-top:40px;max-width:375px;max-height:812px;height:100vh;display:flex;justify-content:center;align-items:flex-start;flex-wrap:wrap}.Calculator i{font-size:26px}.Calculator i>svg{margin-top:5px}.Calculator button:hover>i,.Calculator button:hover>i>svg>path,.Calculator button:hover>span{-webkit-filter:drop-shadow(0px 0px 10px #fff) saturate(240%);filter:drop-shadow(0px 0px 10px #fff) saturate(240%);transition:0.3s ease-in-out}.Calculator button:hover{background-color:rgba(204,204,204,0.11)}.Calculator .Container{width:375px;height:812px;background:#eaebf3;box-shadow:5px 5px 15px #00000018, -5px -5px 15px #ffffff;border-radius:36px;margin:auto;display:flex;align-items:center;flex-direction:column;position:relative;padding-top:30px}.Calculator ::selection{background:#f9b31b3a}.Calculator .Hr{top:80px;height:3px;position:absolute;width:315px;border-radius:5px;box-shadow:inset 0.5px 0.5px 2px rgba(0,0,0,0.11),inset -0.5px -0.5px 2px rgba(255,255,255,0.342)}.Calculator input::-webkit-outer-spin-button,.Calculator input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.Calculator input[type="number"]{-moz-appearance:textfield}.Calculator .Btns{width:300px;display:flex;align-items:center;justify-content:space-between;margin:20px 0 20px 0}.Calculator button{-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;width:62px;height:62px;position:relative;border-radius:18px;padding:5px;box-shadow:5px 5px 15px #0000001f, -5px -5px 15px #ffffffe1;border:0;background-color:#eaebf3;cursor:pointer;-webkit-tap-highlight-color:transparent}.Calculator button:active{box-shadow:1px 1px 5px #0000001f, -1px -1px 5px #ffffffe1;background-color:#e3e4ecce}.Calculator button:focus{outline:0}.Calculator input{font-family:digital-7;width:315px;height:100px;background:#3a3842;border-radius:25px;margin:5px 0 20px 0;padding:20px;box-sizing:border-box;text-align:right;border:none;color:#9597a2;font-size:72px;background:transparent;box-shadow:inset 5px 5px 10px 1px rgba(0,0,0,0.055),inset -5px -5px 10px 0px rgba(255,255,255,0.377);transition:all 0.3s ease-in-out}.Calculator input:focus{outline:none}.Calculator .Placehold{z-index:1;padding-top:28px}.Calculator input::-webkit-input-placeholder{color:#9597a315}.Calculator input:-ms-input-placeholder{color:#9597a315}.Calculator input::-ms-input-placeholder{color:#9597a315}.Calculator input::placeholder{color:#9597a315}.Calculator .Readout,.Calculator .Result{position:absolute;z-index:2;padding-top:28px;background-color:transparent}.Calculator .Result,.Calculator .Result+input.Placehold{height:114px;font-size:56px;padding-top:58px}.Calculator .LastOperation{position:absolute;z-index:2;padding-top:28px;font-size:27px}.Calculator .LastOperation+.Placehold{font-size:27px}.Calculator .LedLastOperation input{box-shadow:none;height:20px;margin-top:5px;padding:10px}.Calculator div.LedLastOperation>input.LastOperation,.Calculator div.LedLastOperation>input.Placehold{padding-right:15px}.Calculator .ResultContainer{position:relative}.Calculator .LedLastOperation{position:absolute;margin-top:25px}.Calculator span{font-family:"Open Sans Condensed", sans-serif;font-weight:bold;font-size:16px;color:#9597a2}.Calculator h1{font-family:"Open Sans Condensed", sans-serif;font-weight:bold;font-size:22px;text-align:left;color:#9597a2;margin-bottom:40px}.Calculator .Top{width:315px}.Calculator img{padding-top:60px;-webkit-filter:grayscale(100%);filter:grayscale(100%)}

